import Present from "./Present";
import "./Presents.css";

const Presents = ({presents, className}) => {
  return (
    <div className={["presents", className].join(" ")}>
      {presents &&
        presents.length > 0 &&
        presents.map((present) => {
          return <Present key={present} />;
        })}
    </div>
  );
};

export default Presents;
