// https://drive.google.com/file/d/1bjqsBa9b1NCKNM9eSb-h5wBGZ_5MB8-4/view?usp=sharing
const audio = new Audio(
  "https://docs.google.com/uc?export=download&id=1bjqsBa9b1NCKNM9eSb-h5wBGZ_5MB8-4"
);
function App() {
  const [showGift, setShowGift] = useState(false);

  useEffect(() => {
    if (showGift) {
      audio.play();
    } else {
      audio.pause();
      audio.currentTime = 0;
    }
  }, [showGift]);
  const onHeartClick = () => {
    setShowGift(true);
  };

  return (
    <div className="App">
      <header className="App-header">
        <FontAwesomeIcon
          className={[
            heartClassNameIcon,
            showGift ? "heart-hidden" : "heart-explode",
          ].join(" ")}
          icon={faHeart}
          color={heartColor}
          size="8x"
        />
        <FontAwesomeIcon
          className={[
            heartClassNameIcon,
            showGift ? "heart-hidden" : "heart-explode-small",
          ].join(" ")}
          icon={faHeart}
          color={heartColor}
          size="8x"
        />
        <FontAwesomeIcon
          className={[heartClassNameIcon, showGift ? "open-gift" : ""].join(
            " "
          )}
          icon={faHeart}
          color={heartColor}
          size="8x"
          onClick={onHeartClick}
        />
        {showGift ? (
          <Presents className="show-presents" presents={presents} />
        ) : (
          <p onClick={onHeartClick} className="heart-title">Your Presents</p>
        )}
      </header>
    </div>
  );
}

export default App;
